<template>
  <div class="tab-panel">
    <div>
      <el-row>
        <el-col :span="8"><div class="grid-content">
          <el-button size="large" style="display: inline-block;margin-left: 2px" @click="addRecordVisible = true"></el-button>
        </div></el-col>
        <el-col :span="16"><div class="grid-content">
          <el-select v-model="searchType" clearable size='large' placeholder="" @change="searchCondition=''">
            <el-option
              v-for="item in typeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <el-input class="large-input" v-model="searchCondition" size="large"
                    @keyup.enter.native="searchGeneral"/>
          <el-button size="large" type="primary"  @click="searchGeneral"></el-button>
        </div></el-col>
      </el-row>
    </div>
    <hr />
    <div class="tab-panel">
      <el-table
        :data="recordList">
        <el-table-column prop="domain_name"
                         label=""
                         width="300px"
                         align="center"/>
        <el-table-column prop="record_type"
                         label=""
                         width="100px"
                         align="center"/>
        <el-table-column prop="record"
                         label=""
                         width="200px"
                         align="center"/>
        <el-table-column prop="ttl"
                         label="TTL"
                         width="100px"
                         align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.ttl===0"></span>
            <span v-else>{{scope.row.ttl}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="zone_name"
                         label="zone"
                         width="140px"
                         align="center"/>
        <el-table-column prop="update_user"
                         label=""
                         align="center"/>
        <el-table-column prop="created_time"
                         label=""
                         align="center">
          <template slot-scope="scope">
            {{scope.row.created_time.substring(0,19)}}
          </template>
        </el-table-column>>
        <el-table-column prop="operaion"
                         label=""
                         align="center">
          <template slot-scope="scope">
            <el-button size="small" @click="openEditDialog(scope.row.domain_name,scope.row.record,scope.row.record_type,scope.row.ttl)"></el-button>
            <el-button type="danger" size="small" @click="deleteRecord(scope.row.domain_name,scope.row.record,scope.row.record_type)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog
      title=""
      :visible.sync="addRecordVisible"
      width="40%">
      <div>
        <div class="input-label"></div>
        <el-input class="large-input" size="large" v-model="addRecordName" placeholder="" @blur="getDefaultTTL"></el-input>
        <el-button size="large" type="primary" style="margin-left: 10px"  @click="addRecordSubmit"></el-button>
        <div class="input-div">
          <div class="input-label"></div>
          <el-checkbox v-model="isAutoBind"/>
        </div>
        <div v-if="isAutoBind">
          <div class="input-div">
            <div class="input-label"></div>
            <el-select v-model="addRecordSubnet" clearable filterable placeholder="">
              <el-option
                v-for="item in regionOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
        </div>
        <div v-if="!isAutoBind">
          <div class="input-div">
            <div class="input-label"></div>
            <el-select class="medium-input" v-model="addRecordType" >
              <el-option label="A" key="A" value="A"></el-option>
              <el-option label="CNAME" key="CNAME" value="CNAME"></el-option>
            </el-select>
            <el-checkbox v-if="addRecordType==='CNAME'" v-model="isCheckRecord"></el-checkbox>
            <span v-if="addRecordType==='CNAME'" class="input-label">CNAME</span>
            <el-checkbox v-if="addRecordType==='A'" v-model="isCheckRecord"></el-checkbox>
            <span v-if="addRecordType==='A'" class="input-label">IP</span>
          </div>
          <div class="input-div">
            <div class="input-label"></div>
            <el-input class="large-input" placeholder="" v-model="addRecordValue"/>
          </div>
          <div class="input-div">
            <div class="input-label">TTL</div>
            <el-input class="medium-input" placeholder="TTL" v-model="addRecordTtl" />
          </div>
          <div class="input-div">
            <div class="input-label">TTL</div>
            <el-input class="medium-input" placeholder="TTL" v-model="defaultTTL" :disabled="true" />
          </div>
        </div>
      </div>
      <div class="tip">
        <div>:</div>
        <div style="color: red">1ACNAME</div>
        <div style="color: red">2CNMAE</div>
        <div style="color: red">3IPippoolIP</div>
        <div>4TTL0TTL</div>
        <div>5</div>
      </div>
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="30%">
      <div>
        <div class="dialog_div">
          <div class="dialog_label"></div>
          <el-input class="xlarge-input" v-model="editName" :disabled="true"/>
        </div>
        <div class="dialog_div">
          <div class="dialog_label"></div>
          <el-select class="medium-input" v-model="editRecord.record_type">
            <el-option label="A" key="A" value="A"></el-option>
            <el-option label="CNAME" key="CNAME" value="CNAME"></el-option>
          </el-select>
          <el-checkbox v-model="editRecord.check"></el-checkbox>
          <span v-if="editRecord.record_type==='CNAME'" class="input-label">CNAME</span>
          <span v-if="editRecord.record_type==='A'" class="input-label">IP</span>
        </div>
        <div class="dialog_div">
          <div class="dialog_label"></div>
          <el-input class="xlarge-input" placeholder="" v-model="editRecord.record" />
        </div>
        <div class="dialog_div">
          <div class="dialog_label">TTL</div>
          <el-input class="xlarge-input" placeholder="TTL" v-model="editRecord.ttl"/>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="modifyRecord"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import util from '@/common/util.js'
  export default {
    name: 'AddRecord',
    data () {
      return {
        typeOptions: [{
          label: '',
          value: 'domain'
        }, {
          label: '',
          value: 'record'
        }],
        searchType: 'domain',
        searchCondition: '',
        recordList: [],

        // 
        addRecordVisible: false,
        regionOptions: [],
        zonesTTL: {},
        defaultTTL: '',
        addRecordName: '',
        addRecordType: 'A',
        addRecordValue: '',
        addRecordTtl: 0,
        addRecordSubnet: '',
        isAutoBind: false,
        isCheckRecord: true,

        editDialogVisible: false,
        editName: '',
        originRecord: {},
        editRecord: {}
      }
    },

    mounted () {
      this.fetchRegionNames()
      this.fetchZonesTTL()
    },

    methods: {
      fetchRegionNames () {
        util.get(this, {
          url: '/web/subnet/list/region',
          succ: (data) => {
            this.regionOptions = util.objArrayToOptions(data.data, 'region_name', 'region_name')
          }
        })
      },

      fetchZonesTTL () {
        util.get(this, {
          url: '/web/record/list/zone_ttl',
          succ: (data) => {
            this.zonesTTL = data.data
          }
        })
      },

      searchGeneral () {
        // ip
        let param = {record: this.searchCondition}
        if (this.searchType === 'domain') {
          param = {domain_name: this.searchCondition}
        }
        util.get(this, {
          url: '/web/record/get/domain_records',
          data: param,
          succ: (data) => {
            console.log(data.data)
            this.recordList = data.data
          }
        })
      },

      getDefaultTTL () {
        this.addRecordName = this.addRecordName.trim()
        if (this.addRecordName === '') {
          return false
        }
        this.defaultTTL = 0
        let tags = this.addRecordName.split('.')
        for (let i in tags) {
          if (i !== 0) {
            let zone = tags.slice(i).join('.')
            if (zone in this.zonesTTL) {
              this.defaultTTL = this.zonesTTL[zone]
              console.log(zone)
              break
            }
          }
        }
        if (this.defaultTTL === 0) {
          util.notify(this, '', 'zone', 'warning')
        }
      },

      addRecordSubmit () {
        this.addRecordName = this.addRecordName.trim()
        this.addRecordValue = this.addRecordValue.trim()
        if (this.addRecordName === '') {
          util.notify(this, '', '', 'error')
          return false
        }
        if (this.isAutoBind) {
          if (this.addRecordSubnet === '') {
            util.notify(this, '', '', 'error')
            return false
          }
        } else {
          if (this.addRecordValue === '') {
            util.notify(this, '', '', 'error')
            return false
          }
        }
        this.$confirm('' + this.addRecordName + '?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.searchType = 'domain'
          this.searchCondition = this.addRecordName
          let url = '/web/record/manually_add_record'
          let param = {domain: this.addRecordName, record: this.addRecordValue, record_type: this.addRecordType, ttl: this.addRecordTtl, check_record: this.isCheckRecord}
          if (this.isAutoBind) {
            url = 'web/record/auto_add_record'
            param = {domain: this.addRecordName, region: this.addRecordSubnet}
          }
          util.post(this, {
            url: url,
            data: param,
            succ: (data) => {
              util.notify(this, '', '')
              this.addRecordVisible = false
              this.searchGeneral()
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },

      openEditDialog (name, record, type, ttl) {
        this.editDialogVisible = true
        this.editName = name
        this.originRecord = {domain_name: name, record: record, record_type: type, ttl: ttl}
        this.editRecord = {domain_name: name, record: record, record_type: type, ttl: ttl, check: true}
      },

      modifyRecord () {
        let update = {}
        this.editRecord.record = this.editRecord.record.trim()
        if (this.editRecord.record === '') {
          util.notify(this, '', '', 'error')
          return false
        }
        this.editRecord.ttl = parseInt(this.editRecord.ttl)
        if (typeof this.editRecord.ttl !== 'number' || this.editRecord.ttl < 0) {
          util.notify(this, '', 'TTL0', 'error')
          return false
        }
        if (this.editRecord.record === this.originRecord.record && this.originRecord.record_type !== this.editRecord.record_type) {
          util.notify(this, '', '', 'error')
          return false
        }

        if (this.editRecord.record !== this.originRecord.record) {
          update['record_type'] = this.editRecord.record_type
          update['record'] = this.editRecord.record
          update['check_record'] = this.editRecord.check
        }
        if (this.editRecord.ttl !== this.originRecord.ttl) {
          update['ttl'] = this.editRecord.ttl
        }
        if (JSON.stringify(update) === '') {
          util.notify(this, '', '', 'error')
        }
        console.log(update)

        this.$confirm('' + name + '?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          util.post(this, {
            url: '/web/record/modify_record',
            data: {domain_name: this.editName, origin_record: this.originRecord.record, update_dict: update},
            succ: (data) => {
              util.notify(this, '', '')
              this.editDialogVisible = false
              this.searchGeneral()
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },

      deleteRecord (name, records, type) {
        this.$confirm('' + name + '?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          util.post(this, {
            url: '/web/record/delete',
            data: {domain_name: name, record: records, record_type: type},
            succ: (data) => {
              util.notify(this, '', '')
              this.searchGeneral()
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      }
    }
  }
</script>

<style scoped>
  .input-div {
    margin-top: 15px ;
  }

  .input-label {
    display: inline-block;
    width: 100px;
    margin-right: 10px;
  }

  .tip {
    color: blue;
    margin-top: 30px
  }
</style>



