<template>
  <div>
    <div>
      <el-tabs v-model="ipActiveName"  @tab-click="handleIpTabClick">
        <el-tab-pane label="" name="operation">
          <div class="tab-panel">
            <el-input class="large-input" size="large" placeholder="ip" v-model="ipSearch"
                      @keyup.enter.native="searchIp"/>
            <el-button size="large" @click="searchIp"></el-button>
            <el-button v-if="ipStateList.length!==0&&canOff" size="large" type="warning" @click="switchIpToAqb" ></el-button>
            <el-button v-if="ipStateList.length!==0&&canOff" size="large" type="danger" @click="replaceIp">IP</el-button>
          </div>
          <div class="tab-panel">
            <el-table
              class="medium-table"
              v-loading="searchIpLoading"
              border
              :data="ipStateList">
              <el-table-column prop="domain_name"
                               label=""
                               width="300px"
                               align="center"/>
              <el-table-column
                prop="active"
                label="IP"
                align="center">
                <template slot-scope="scope">
                      <span class="table-item" v-for="isp in scope.row.active">
                        {{ isp }}
                      </span>
                  <span v-if="scope.row.active.length===0">
                        
                      </span>
                </template>
              </el-table-column>
              <el-table-column
                prop="has_aqb"
                label=""
                width="100px"
                align="center">
                <template slot-scope="scope">
                      <span v-if="scope.row.has_aqb">
                        
                      </span>
                  <span v-else style="color: red">
                        
                      </span>
                </template>
              </el-table-column>
              <el-table-column
                prop="only_record"
                label=""
                width="100px"
                align="center">
                <template slot-scope="scope">
                      <span v-if="scope.row.only_record" style="color:red;">
                        
                      </span>
                  <span v-else >
                        
                      </span>
                </template>
              </el-table-column>
              <el-table-column
                prop="has_switched"
                label=""
                width="100px"
                align="center">
                <template slot-scope="scope">
                      <span v-if="scope.row.has_switched==='forbid'" style="color:red;">
                        
                      </span>
                  <span v-if="scope.row.has_switched==='replace'" style="color: red">
                        
                      </span>
                  <span v-if="scope.row.has_switched==='no_switch'" >
                        
                      </span>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-tab-pane>
        <el-tab-pane class="tab-panel" label="" name="record">
          <el-table
            border
            :data="switchHistory">
            <el-table-column prop="id"
                             label=""
                             width="80px"
                             align="center"/>
            <el-table-column prop="rtx_id"
                             label=""
                             width="150px"
                             align="center"/>
            <el-table-column prop="update_at"
                             label=""
                             width="200px"
                             align="center"/>
            <el-table-column prop="switch_ip"
                             label="IP"
                             width="240px"
                             align="center"/>
            <el-table-column prop="switch_type"
                             label=""
                             width="240px"
                             align="center">
              <template slot-scope="scope">
                <span v-if="scope.row.switch_type==='aqb'"></span>
                <span v-if="scope.row.switch_type==='replace'&&scope.row.switch_to===''">IP</span>
                <span v-if="scope.row.switch_type==='replace'&&scope.row.switch_to!==''">IP</span>
              </template>
            </el-table-column>
            <el-table-column prop="switch_to"
                             label=""
                             width="200px"
                             align="center">
              <template slot-scope="scope">
                <span v-if="scope.row.switch_to==='aqb'"></span>
                <span v-if="scope.row.switch_type==='replace'&&scope.row.switch_to===''">IP</span>
                <span v-if="scope.row.switch_type==='replace'&&scope.row.switch_to!==''">{{scope.row.switch_to}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="state"
                             label=""
                             align="center">
              <template slot-scope="scope">
                <span v-if="scope.row.state==='switched'" style="color: blue"></span>
                <span v-if="scope.row.state==='recovered'"></span>
              </template>
            </el-table-column>>
            <el-table-column prop="operation"
                             label=""
                             width="250px"
                             align="center">
              <template slot-scope="scope">
                <el-button size="small"
                           v-if="scope.row.state==='switched'&&scope.row.switch_type==='aqb'"
                           type="info"
                           :disabled="scope.row.state==='recovered'"
                           @click="switchIpFromAqb(scope.row.id)">
                  
                </el-button>
                <el-button size="small"
                           v-if="scope.row.state==='switched'&&scope.row.switch_type==='replace'&&scope.row.switch_to!==''"
                           type="info" :disabled="scope.row.state==='recovered'"
                           @click="cancelReplaceIp(scope.row.id)">
                  
                </el-button>
                <el-button size="small"
                           v-if="scope.row.state==='switched'&&scope.row.switch_type==='replace'&&scope.row.switch_to===''"
                           type="info" :disabled="scope.row.state==='recovered'"
                           @click="cancelReplaceIp(scope.row.id)">
                  
                </el-button>
                <el-button size="small"
                           v-if="scope.row.switch_type==='aqb'"
                           @click="viewSwitchDetail(scope.row.id)">
                  
                </el-button>
                <span v-if="scope.row.state==='recovered'&&scope.row.switch_type==='replace'"></span>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </div>
    <el-dialog
      title=""
      :visible.sync="aqbVisible">
    <span>
      <div v-if="noAqbList.length===0"></div>
      <div v-if="noAqbList.length!==0"></div>
      <div v-for="domain in noAqbList">
        <div style="width:30px;margin-top: 5px;">{{domain}}</div>
      </div>
    </span>
      <span slot="footer" class="dialog-footer">
      <el-button type="primary" @click="handleCloseAqbDialog"></el-button>
    </span>
    </el-dialog>
    <el-dialog
      title="IP"
      :visible.sync="ipVisible">
      <el-input style="display: inline-block" v-model="switchTo" placeholder="ip,IP."></el-input>
      <span slot="footer" class="dialog-footer">
      <el-button type="primary" @click="handleCloseIpDialog"></el-button>
    </span>
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="detailVisible"
      width="90%">
      <el-table
        style="margin-top: 5px"
        :data="switchDetails">
        <el-table-column prop="domain_name"
                         label=""
                         width="350px"
                         align="center"/>
        <el-table-column prop="isp"
                         label=""
                         width="150px"
                         align="center"/>
        <el-table-column prop="before_enabled_server_rooms"
                         label=""
                         align="center"/>
        <el-table-column prop="before_state"
                         label="ID"
                         align="center"/>
        <el-table-column prop="after_state"
                         label="ID"
                         align="center"/>
      </el-table>
      <el-pagination
        align="right"
        layout="prev, pager, next"
        @current-change="handleDetailChange"
        :current-page="detailPage"
        :page-size="detailPageSize"
        :total="detailTotal">
      </el-pagination>
    </el-dialog>
  </div>
</template>

<script>
  let domain = ''
  export default {
    name: 'IpManager',
    data () {
      return {
        ipActiveName: 'operation',

        ipSearch: '',
        searchIpLoading: false,
        ipStateList: [],
        canOff: false,
        aqbVisible: false,
        ipVisible: false,
        switchTo: '',
        noAqbList: [],
        switchHistory: [],

        detailVisible: false,
        detailPage: 1,
        detailPageSize: 18,
        detailTotal: 0,
        switchDetails: [],
        switchId: 0,

        intervalId: -1
      }
    },

    mounted () {
      this.intervalId = setInterval(() => {
        if (this.ipActiveName === 'record') {
          this.fetchSwitchHistory()
        }
      }, 2000)
    },

    destroyed () {
      clearInterval(this.intervalId)
    },

    methods: {
      handleIpTabClick (tab, event) {
        if (tab.$data.index === '1') {
          this.fetchSwitchHistory()
        }
      },

      searchIp () {
        if (this.ipSearch === '') {
          this.$notify.error({
            title: 'IP',
            message: 'IP',
            duration: 1500
          })
          return false
        }
        this.searchIpLoading = true
        this.$http.post(domain + 'ui/list_ip_state', {ip: this.ipSearch})
          .then((res) => {
            this.ipStateList = res.data.data
            this.canOff = false
            for (let i in this.ipStateList) {
              if (this.ipStateList[i].active.length !== 0) {
                this.canOff = true
              }
            }
            this.searchIpLoading = false
          })
          .catch((err) => {
            this.searchIpLoading = false
            console.log(err)
          })
      },

      switchIpToAqb () {
        this.$confirm('ip', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$http.post(domain + 'ui/switch_ip_to_aqb', {ip: this.ipSearch})
            .then((res) => {
              if (res.data.data === 'no_domain') {
                this.$notify.error({
                  title: '',
                  message: '!',
                  duration: 1500
                })
              } else if (res.data.data === 'ip_switched') {
                this.$notify.error({
                  title: '',
                  message: 'IP!',
                  duration: 1500
                })
              } else {
                this.noAqbList = res.data.data
                this.aqbVisible = true
              }
            })
            .catch((err) => {
              console.log(err)
              this.$notify.error({
                title: '',
                message: '',
                duration: 1500
              })
            })
        }).catch(() => {
          this.$notify.info({
            title: '',
            message: '',
            duration: 1500
          })
        })
      },

      fetchSwitchHistory () {
        this.$http.get(domain + '/ui/list_switch_history')
          .then((res) => {
            this.switchHistory = res.data.data
          })
          .catch((err) => {
            console.log(err)
          })
      },

      switchIpFromAqb (switchId) {
        this.$confirm('ip', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$http.post(domain + 'ui/switch_ip_from_aqb', {switch_id: switchId})
            .then((res) => {
              this.searchIp()
              this.$notify.success({
                title: '',
                message: '',
                duration: 1500
              })
            })
            .catch((err) => {
              console.log(err)
              this.$notify.error({
                title: '',
                message: '',
                duration: 1500
              })
            })
        }).catch(() => {
          this.$notify.info({
            title: '',
            message: '',
            duration: 1500
          })
        })
      },

      replaceIp () {
        if (this.ipSearch === '') {
          this.$notify.error({
            title: '',
            message: 'ip',
            duration: 1500
          })
        } else {
          this.ipVisible = true
        }
      },

      cancelReplaceIp (switchId) {
        this.$confirm('ip', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$http.post(domain + 'ui/cancel_replace_ip', {switch_id: switchId})
            .then((res) => {
              let msg = res.data.data
              if (msg === 'ok') {
                this.searchIp()
                this.$notify.success({
                  title: '',
                  message: '',
                  duration: 1500
                })
              } else {
                this.$notify.error({
                  title: '',
                  message: '',
                  duration: 1500
                })
              }
            })
            .catch((err) => {
              console.log(err)
              this.$notify.error({
                title: '',
                message: '',
                duration: 1500
              })
            })
        }).catch(() => {
          this.$notify.info({
            title: '',
            message: '',
            duration: 1500
          })
        })
      },

      viewSwitchDetail (switchId) {
        this.switchId = switchId
        this.$http.post(domain + '/ui/view_switch_detail', {switch_id: switchId, page: this.detailPage, page_size: this.detailPageSize})
          .then((res) => {
            this.switchDetails = res.data.data
            this.detailTotal = res.data.total
            this.detailVisible = true
          })
          .catch((err) => {
            console.log(err)
            this.$notify.error({
              title: '',
              message: '!',
              duration: 1500
            })
          })
      },

      handleDetailChange (val) {
        this.detailPage = val
        this.viewSwitchDetail(this.switchId)
      },

      handleCloseIpDialog () {
        this.$http.post(domain + '/ui/replace_ip', {switch_ip: this.ipSearch, switch_to: this.switchTo})
          .then((res) => {
            let msg = res.data.data
            if (msg === 'ok') {
              this.$notify.success({
                title: '',
                message: 'IP',
                duration: 1500
              })
            } else if (msg === 'ip_switched') {
              this.$notify.error({
                title: '',
                message: 'IP',
                duration: 1500
              })
            } else {
              this.$notify.error({
                title: '',
                message: 'IP',
                duration: 1500
              })
            }
            this.ipActiveName = 'record'
            this.ipVisible = false
          })
          .catch((err) => {
            this.$notify.error({
              title: '',
              message: 'IP',
              duration: 1500
            })
            console.log(err)
            this.ipVisible = false
          })
      },

      handleCloseAqbDialog () {
        this.aqbVisible = false
        this.searchIp()
      }
    }
  }
</script>

<style scoped>
  .table-item {
    line-height: 30px;
    height: 30px;
    margin-left: 3px;
    margin-bottom: 2px;
    margin-top: 2px;
    padding:2px;
    background:aliceblue;
    overflow: visible;
    display: inline-table;
  }

</style>



